.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

.AppContainer {
  background-image: url('../public/images/background/22358574.jpg');
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  background-attachment: fixed; /* 背景を固定してスクロールやリサイズの影響を防ぐ */
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

/* スライドアニメーション（下から上） */
.battle-result-item {
  opacity: 0;
  transform: translateY(20px);
  animation: slideUp 0.4s ease-out forwards;
}

@keyframes slideUp {
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes zoomIn {
  from {
    opacity: 0;
    transform: scale(0.5);
  }
  to {
    opacity: 1;
    transform: scale(1);
  }
}

@keyframes pulse {
  0% {
    transform: scale(1);
    opacity: 1;
  }
  50% {
    transform: scale(1.1);
    opacity: 0.8;
  }
  100% {
    transform: scale(1);
    opacity: 1;
  }
}

@keyframes shimmer {
  0% {
    left: -100%;
  }
  100% {
    left: 100%;
  }
}

@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

/* クラッカー（紙吹雪）アニメーション */
.confetti {
  position: fixed;
  width: 10px;
  height: 10px;
  background-color: #f00;
  z-index: 9999;
  pointer-events: none;
}

@keyframes confetti-fall {
  0% {
    transform: translate(0, 0) rotate(0deg);
    opacity: 1;
  }
  100% {
    transform: translate(var(--x-dest), var(--y-dest)) rotate(var(--rotation));
    opacity: 0;
  }
}

/* シナジー発生時の激しい発光アニメーション */
@keyframes synergy-glow {
  0% {
    box-shadow: 0 0 15px #ffeb3b, inset 0 0 10px #ffeb3b;
    border-color: #ffeb3b;
  }
  50% {
    box-shadow: 0 0 30px #ffff00, inset 0 0 20px #ffff00;
    border-color: #ffffff;
  }
  100% {
    box-shadow: 0 0 15px #ffeb3b, inset 0 0 10px #ffeb3b;
    border-color: #ffeb3b;
  }
}

.synergy-active {
  animation: synergy-glow 1.5s infinite alternate;
}

/* PC版の基本スタイル */
.skill-card {
  width: 100px;
  display: flex;
  flex-direction: column;
  align-items: center;
  box-sizing: border-box;
}

.skill-icon {
  width: 50px;
  height: 50px;
  margin-bottom: 5px;
}

.skill-name {
  font-size: 14px;
  font-weight: bold;
}

@media (min-width: 601px) {
  .skill-card-grid {
    grid-template-columns: repeat(5, 1fr); /* 5列表示 */
    display: grid;
    gap: 10px; /* カード間のスペース */
    justify-items: center;
  }
}

@media (max-width: 600px) {
  .AppContainer {
    flex-direction: column !important;
    height: 100% !important; /* スマホ版での高さ問題を修正 */
    min-height: 100vh !important;
    overflow-y: auto !important;
    overflow-x: hidden;
  }
  .MainGameArea {
    flex: none !important;
    width: 100% !important;
    max-width: 100vw !important;
    padding: 10px !important;
    box-sizing: border-box;
    overflow: visible !important; /* スクロール可能にする */
  }
  .GameLogFrame {
    flex: none !important;
    width: 100% !important;
    height: auto !important;
    min-height: 400px;
    max-height: 80vh; /* ログが長すぎても画面を覆い尽くさないように */
    border-left: none !important;
    border-top: 1px solid #333;
    box-sizing: border-box;
    overflow-y: auto !important;
    padding-bottom: 50px !important; /* 下部の余白を確保 */
  }
  
  /* スマホ版のみカードを縮小 */
  .skill-card {
    width: 58px !important; /* 5枚並べるために微調整 */
    min-width: 58px !important;
    padding: 4px !important;
    margin: 1px !important;
  }
  .skill-icon {
    width: 30px !important;
    height: 30px !important;
  }
  .skill-name {
    font-size: 9px !important;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    width: 100%;
    text-align: center; /* 中央揃え */
  }

  .skill-card-grid {
    grid-template-columns: repeat(4, 1fr) !important; /* 所持スキル画面は4列 */
    display: grid !important;
    gap: 6px !important;
    width: 100% !important;
    justify-items: center;
  }

  .skill-card-grid .skill-card {
    width: 78px !important; /* 4枚並べるために最大化 */
    min-width: 78px !important;
    padding: 6px !important;
  }
  .skill-card-grid .skill-icon {
    width: 40px !important;
    height: 40px !important;
  }
  .skill-card-grid .skill-name {
    font-size: 11px !important;
  }

  .SelectedSkillsPanel {
    padding: 10px 5px !important;
    width: 100% !important;
    box-sizing: border-box !important;
  }

  .boss-skill-grid {
    display: grid !important;
    grid-template-columns: repeat(5, 1fr) !important; /* ボススキルは5列 */
    gap: 2px !important;
    width: 100% !important;
    justify-items: center;
  }

  /* 選択済みスキルの親要素も5列にするための調整 */
  /* 5列表示のエリアは元の小さいサイズを維持 */
  .SelectedSkillsPanel > div, 
  .boss-skill-grid {
    display: grid !important;
    grid-template-columns: repeat(5, 1fr) !important;
    gap: 2px !important;
    justify-items: center;
    width: 100% !important;
  }
  .SelectedSkillsPanel .skill-card,
  .boss-skill-grid .skill-card {
    width: 58px !important;
    min-width: 58px !important;
    padding: 4px !important;
  }
  .SelectedSkillsPanel .skill-icon,
  .boss-skill-grid .skill-icon {
    width: 30px !important;
    height: 30px !important;
  }
  .SelectedSkillsPanel .skill-name,
  .boss-skill-grid .skill-name {
    font-size: 9px !important;
  }
  .boss-skill-card-wrapper {
    width: 100% !important;
    display: flex;
    justify-content: center;
  }
  
  .PlayerSkillSelection {
    width: 100% !important;
    padding: 10px 5px !important;
    box-sizing: border-box !important;
  }
  .PlayerSkillSelection h2 {
    font-size: 1.2rem !important;
  }

  /* 報酬選択画面用の調整 (1列3枚で拡大) */
  .RewardSelection .skill-card {
    width: 90px !important;
    min-width: 90px !important;
    padding: 8px !important;
  }
  .RewardSelection .skill-icon {
    width: 45px !important;
    height: 45px !important;
  }
  .RewardSelection .skill-name {
    font-size: 11px !important;
    white-space: normal !important; /* 報酬画面では改行を許可して見やすく */
  }

  /* ログ画面の「VS」ヘッダーのスマホ調整 */
  .battle-start-header {
    margin: 15px 0 !important;
    padding: 10px 0 !important;
  }
  .battle-start-header > div:first-child {
    font-size: 0.8rem !important;
    margin-bottom: 5px !important;
  }
  .battle-start-player-name,
  .battle-start-enemy-name {
    font-size: 1.1rem !important;
  }
  .battle-start-vs {
    font-size: 1.5rem !important;
  }
}

/* ボスバトルアニメーション */
.boss-battle-image {
  transition: transform 0.2s ease-out, filter 0.2s;
}

.boss-anim-attack {
  animation: bossAttack 0.5s ease-in-out;
}

.boss-anim-damage {
  animation: bossDamage 0.5s ease-in-out;
  filter: sepia(1) saturate(5) hue-rotate(-50deg); /* 赤っぽくフラッシュ */
}

.boss-anim-counter {
  animation: bossCounter 0.8s ease-in-out;
  filter: brightness(1.5) drop-shadow(0 0 15px gold);
}

.boss-anim-defeat {
  filter: grayscale(1) opacity(0.5);
  transform: scale(0.9) translateY(20px);
}

@keyframes bossAttack {
  0% { transform: scale(1); }
  50% { transform: scale(1.3) translateY(20px); }
  100% { transform: scale(1); }
}

@keyframes bossDamage {
  0%, 100% { transform: translateX(0); }
  20%, 60% { transform: translateX(-10px); }
  40%, 80% { transform: translateX(10px); }
}

@keyframes bossCounter {
  0% { transform: scale(1); }
  20% { transform: scale(0.8); }
  50% { transform: scale(1.5); }
  100% { transform: scale(1); }
}

.damage-flash {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(255, 0, 0, 0.3);
  animation: flashEffect 0.3s ease-out forwards;
  pointer-events: none;
  z-index: 10;
}

@keyframes flashEffect {
  from { opacity: 1; }
  to { opacity: 0; }
}

.boss-skill-grid {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 10px;
  width: 100%;
}

.battle-results-scroll-container {
  max-height: 50vh; /* 画面高さの半分程度に制限 */
  max-width: 800px;
  padding: 10px;
  overflow-y: auto;
  border: 1px solid #333;
  border-radius: 8px;
  background-color: #121212;
  width: 100%;
  box-sizing: border-box;
}

/* リッチログ用のスタイル */
.rich-log {
  font-family: "MS UI Gothic", "Arial", sans-serif;
  background-color: #bbccbb;
  color: #333;
  padding: 20px;
  border-radius: 10px;
}

.rich-log .phase {
  font-weight: bold;
  color: #2e7d32;
  border-left: 5px solid #2e7d32;
  padding-left: 10px;
  margin: 10px 0;
}

.rich-log .action {
  color: #d32f2f;
  font-weight: bold;
}

.rich-log .counter {
  color: #1976d2;
  font-style: italic;
}

.rich-log table {
  width: 100%;
  border-collapse: collapse;
  margin: 10px 0;
  background-color: rgba(255, 255, 255, 0.3);
}

.rich-log th, .rich-log td {
  padding: 5px;
  border: 1px solid #99aa99;
  text-align: center;
}

.rich-log hr {
  border: none;
  border-top: 1px dashed #99aa99;
  margin: 10px 0;
}
